$titulo-categoria: 1rem;
$separado: 1rem;

$icolor1: #FBB117;
$icolor2: #ff611d;
$icolor2-active: #ffa04d;

@font-face {
    font-family: 'Montserrat';
    src: url("./fonts/SFNSText-Regular.otf"), url(https://fonts.gstatic.com/s/pacifico/v12/FwZY7-Qmy14u9lezJ-6H6MmBp0u-.woff2);
    font-display: swap;

}



@font-face {
    font-family: 'Dancing Script';
    font-style: normal;
    font-weight: 700;
    font-display: swap;
    src: url(https://fonts.gstatic.com/s/dancingscript/v24/If2cXTr6YS-zF4S-kcSWSVi_sxjsohD9F50Ruu7B1i03Sup8.woff2) format('woff2');
    unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}





body {
    font-family: 'Montserrat';
    margin: 0;
    background-color: #f4efeb;
    display: flex;
    justify-content: center;

    @media (max-width: 700px) {
        margin: 0;
        padding: 0;
    }

    user-select: none;
}


.buscar {
    position: absolute;
    margin: auto;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 300px;
    height: 100px;

    .search {
        position: absolute;
        margin: auto;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        width: 80px;
        height: 80px;
        background: transparent;
        border-radius: 50%;
        transition: all 0.5s;
        z-index: 4;

        &:hover {
            cursor: pointer;
        }

        &::before {
            content: "";
            position: absolute;
            margin: auto;
            top: 22px;
            right: 0;
            bottom: 0;
            left: 22px;
            width: 12px;
            height: 2px;
            background: white;
            transform: rotate(45deg);
            transition: all 0.5s;
        }

        &::after {
            content: "";
            position: absolute;
            margin: auto;
            top: -5px;
            right: 0;
            bottom: 0;
            left: -5px;
            width: 25px;
            height: 25px;
            border-radius: 50%;
            border: 2px solid white;
        }
    }

    input {
        font-family: "Open Sans", Arial, Verdana;
        position: absolute;
        margin: auto;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        width: 35px;
        height: 35px;
        outline: none;
        border: none;
        background: rgb(55, 55, 55);
        color: rgb(203, 203, 203);
        padding: 0 80px 0 20px;
        border-radius: 30px;
        transition: all 0.5s;
        opacity: 0;
        z-index: 5;

        &:hover {
            cursor: pointer;
        }

        &:focus {
            width: 200px;
            opacity: 1;
            cursor: text;
        }

        &:focus~.search {
            right: -250px;
            z-index: 6;

            &::before {
                top: 0;
                left: 0;
                width: 25px;
                background: rgb(255, 255, 255);
            }

            &::after {
                top: 0;
                left: 0;
                width: 25px;
                height: 2px;
                border: none;
                background: rgb(255, 255, 255);
                border-radius: 0%;
                transform: rotate(-45deg);
            }
        }
    }
}


.contenido-anterior {
    margin-bottom: 4.5rem;
}

.menuGrande {
    box-shadow: 2px 4px 10px #333339;
    width: 100vw;
    max-width: 700px;
    background-color: rgb(27, 27, 27);

}

@media (max-width: 700px) {
    .menuGrande {

        box-shadow: none;
    }
}

.menu {
    background-color: rgb(27, 27, 27);
    aspect-ratio: 100 / 29;

    @media (max-width: 700px) {

        padding: 1rem;
    }

    padding: 3rem;
}


.image-button {
    width: 100px;
    height: 100px;
    background-image: url('./cerveza.webp');
    background-size: cover;
    background-position: center;
    transition: background-size 0.5s;
}

.sticky-footer {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 58px;
    background: #181818;
    position: fixed;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
