    width: 100%;
}



.categoria-seleccionada {

    text-align: center;
    color: rgb(255, 255, 255);
    font-size: 1.7rem;
    font-family: 'Dancing Script';
    margin-bottom: -1.5rem;
    margin-top: -2rem;

    @media (max-width: 700px) {
