        margin-top: -1.5rem;
        font-size: 1.3rem;
    }
}

.header__logo {
    width: 80%;
    height: 80%;
    filter: drop-shadow(1rem 1rem 1rem #000000);
}


.vintage-container {
    position: relative;

}

.vintage-container::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('./textura.webp');

    @media (max-width: 700px) {
        opacity: 0.45;
    }

    background-repeat: repeat;
    opacity: 0.5;
    pointer-events: none;
}






.titulo-categoria {

    margin-top: 1.5rem;
    color: rgb(255, 255, 255);
    margin-bottom: 2rem;
}


