.item-description {
    position: relative;
    color: rgb(163, 163, 163);
    font-size: 0.71rem;
    margin-top: 0.2rem;
    margin-bottom: 1rem;

    @media (max-width: 700px) {
        font-size: 0.54rem;
    }

    @media (max-width: 414px) {
        font-size: 0.47rem;
    }
}

.item-description:after {
    position: absolute;
    content: '';
    border-bottom: 1px solid rgb(40, 40, 40);
    width: 100%;
    transform: translateX(-50%);
    bottom: -5px;
    left: 50%;
}


.items2 {
    columns: 1;
    margin-bottom: 1;

    @media (max-width: 700px) {}
}

.error {
    text-align: center;
}

.items {
