}

.precio {
    border-radius: 4px;

    padding: 1px 2px 1px 2px;
    background-color: rgba(159, 112, 19, 0.5);
    color: rgb(210, 210, 210);
    font-size: 1rem;

    @media (max-width: 700px) {
        font-size: 0.6rem;
        padding: 1px 2px 1px 2px;
    }

    @media (max-width: 414px) {
        font-size: 0.56rem;
    }
}

span {
    flex-grow: 1;
}

.nombre-item {
    margin-top: 0;
    font-size: 1rem;

    @media (max-width: 700px) {
        font-size: 0.6rem;
    }

    @media (max-width: 414px) {
        font-size: 0.55rem;
    }
}

.precio-nombre {
    margin-bottom: -0.7rem;
    display: flex;
}


h2 {
    font-size: 1rem;
    color: rgb(212, 212, 212);
}


.menu-card {
    min-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}



h3 {

    margin: 0 0 10px;
    font-weight: bold;
}


.container {
    top: 0;
    left: 0;
    right: 0;
    display: grid;
    grid-template-columns: 0% auto 0%;
    grid-template-rows: 4rem 80px auto 100px 30px;

    @media (max-width: 700px) {
        margin-top: 1rem;
        grid-template-rows: 4rem 80px auto 100px 30px;
    }
}


.indicators {
    z-index: 0;
    grid-column-start: 2;
    grid-column-end: 2;
    grid-row-start: 5;
    grid-row-end: 5;
    display: grid;

    grid-template-columns: 10% 40% 40% 10%;
    grid-template-rows: 10px;
}


#i2,
#i3 {
    grid-column-start: 1;
    grid-column-end: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

#i2 {
    grid-column-start: 2;
    grid-column-end: 2;
}

#i3 {
    grid-column-start: 3;
    grid-column-end: 3;
}


.navi-indicator1 {
    box-shadow: 2px 4px 10px $icolor1;
    height: 4px;
    width: 60px;
    border-radius: 20px;
    transition: width 0.3s;
}

.navi-indicator1-inactive {
    width: 0px;
}

.navi-indicator2 {
    box-shadow: 2px 4px 10px $icolor2;
    height: 4px;
    width: 60px;
    border-radius: 20px;
    transition: width 0.3s;
}


#ni1 {
    background: $icolor1;
}

#ni2 {
    background: $icolor2;
}

.navi {
    grid-column-start: 2;
    grid-column-end: 2;
    grid-row-start: 4;
    grid-row-end: 4;
    display: grid;
    grid-template-columns: 10% 40% 40% 10%;
    grid-template-rows: 100px;
}


.navi-item2,
.navi-item3 {
    grid-column-start: 1;
    grid-column-end: 2;
    display: flex;
    align-items: center;
    justify-content: center;
}

.navi-item2 {
    grid-column-start: 2;
    grid-column-end: 2;


}

.navi-item3 {
    grid-column-start: 3;
    grid-column-end: 3;
}

.nav-button {
    z-index: 1;
    transition: transform 250ms;
    height: 6rem;
    width: 6rem;
    border-radius: 20px;
    font-size: 1rem;
    background: linear-gradient(145deg, rgb(35, 35, 35), rgb(27, 27, 27));

    @media (max-width: 700px) {
        box-shadow: 4px 3px 8px rgb(2, 2, 2),
            -6px -6px 10px rgb(24, 24, 24);
        height: 5rem;
        width: 5rem;

        font-size: 0.9rem;
    }

    box-shadow: 6px 6px 10px rgb(0, 0, 0),
    -6px -6px 10px #1a1a1a;
    font-weight: 700;
    cursor: pointer;
}



.nav-button.active {
    transform: translateY(-10px);
}

button,
button-p {
    display: inline-block;
    border: none;
    margin: 0;
    text-decoration: none;
    padding: 0;
    background: none;
    border-radius: 0;
    overflow: hidden;
    user-select: none;
    -webkit-tap-highlight-color: transparent;
}